/* Animation for 3D box rolling effect */
@keyframes rollIn3D {
  0% {
    transform: translateX(-100vw) rotateY(90deg);
    opacity: 0;
  }
  60% {
    transform: translateX(0) rotateY(-10deg);
    opacity: 1;
  }
  100% {
    transform: translateX(0) rotateY(0deg);
    opacity: 1;
  }
}

/* Animation for text sliding out of the box */
@keyframes slideOutFromBox {
  0% {
    transform: translateX(-50%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

/* Apply the 3D rolling animation to images */
.box-roll {
  animation: rollIn3D 2s ease-out forwards;
  animation-delay: 0s;
}

/* Apply the text sliding animation */
.text-box {
  animation: slideOutFromBox 1s ease-out forwards;
  animation-delay: 2s; /* Start after the image has stopped rolling */
  opacity: 0; /* Initially hidden */
}

/* Add visibility once animation starts */
.text-box.hidden {
  opacity: 0;
  animation-delay: 2s;
  animation-fill-mode: forwards;
}
